"use client"

import { useState } from "react"
import { motion } from "framer-motion"

export default function Contact() {
  const [s, setS] = useState({ name: "", email: "", message: "" })
  const [status, setStatus] = useState("")
  const [focused, setFocused] = useState("")

  async function submit(e) {
    e.preventDefault()
    setStatus("Sending...")

    try {
      const res = await fetch("/api/contact", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(s),
      })

      if (res.ok) {
        setStatus("Message Sent ‚úÖ")
        setS({ name: "", email: "", message: "" })
        setTimeout(() => setStatus(""), 3000)
      } else {
        setStatus("Error ‚ùå")
        setTimeout(() => setStatus(""), 3000)
      }
    } catch {
      setStatus("Error ‚ùå")
      setTimeout(() => setStatus(""), 3000)
    }
  }

  return (
    <section id="contact" className="py-24 bg-gradient-to-b from-slate-50 to-white relative overflow-hidden">
      
      {/* Decorative Background Elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <motion.div
          animate={{ 
            scale: [1, 1.2, 1],
            rotate: [0, 90, 0]
          }}
          transition={{ duration: 20, repeat: Infinity }}
          className="absolute -top-40 -right-40 w-96 h-96 bg-green-200/30 rounded-full blur-3xl"
        />
        <motion.div
          animate={{ 
            scale: [1, 1.3, 1],
            rotate: [0, -90, 0]
          }}
          transition={{ duration: 25, repeat: Infinity }}
          className="absolute -bottom-40 -left-40 w-96 h-96 bg-blue-200/30 rounded-full blur-3xl"
        />
      </div>

      <div className="max-w-6xl mx-auto px-6 relative z-10">

        {/* Section Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6 }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl font-bold text-slate-900 mb-4">
            Get In Touch
          </h2>
          <p className="text-slate-600 text-lg max-w-2xl mx-auto">
            Have a project in mind? We'd love to hear from you. Send us a message and we'll respond as soon as possible.
          </p>
        </motion.div>

        <div className="grid md:grid-cols-2 gap-12 items-start">

          {/* Contact Information */}
          <motion.div
            initial={{ opacity: 0, x: -50 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.7 }}
            className="space-y-8"
          >
            <div>
              <h3 className="text-2xl font-bold text-slate-900 mb-6">
                Let's Talk
              </h3>
              <p className="text-slate-600 leading-relaxed mb-8">
                Whether you have a question about features, pricing, need a demo, or anything else, our team is ready to answer all your questions.
              </p>
            </div>

            {/* Contact Info Cards */}
            <div className="space-y-4">
              {[
                {
                  icon: "üìß",
                  label: "Email",
                  value: "hello@venturemond.com",
                  link: "mailto:hello@venturemond.com"
                },
                {
                  icon: "üìû",
                  label: "Phone",
                  value: "+1 (555) 123-4567",
                  link: "tel:+15551234567"
                },
                {
                  icon: "üìç",
                  label: "Office",
                  value: "123 Business St, Tech City, TC 12345",
                  link: "#"
                }
              ].map((item, idx) => (
                <motion.a
                  key={item.label}
                  href={item.link}
                  initial={{ opacity: 0, x: -30 }}
                  whileInView={{ opacity: 1, x: 0 }}
                  viewport={{ once: true }}
                  transition={{ duration: 0.5, delay: idx * 0.1 }}
                  whileHover={{ x: 10 }}
                  className="flex items-start gap-4 p-4 bg-white rounded-xl shadow-sm hover:shadow-md transition-all duration-300 border border-slate-200 group"
                >
                  <div className="text-3xl">{item.icon}</div>
                  <div>
                    <div className="text-sm font-semibold text-slate-500 mb-1">{item.label}</div>
                    <div className="text-slate-900 font-medium group-hover:text-green-600 transition-colors">
                      {item.value}
                    </div>
                  </div>
                </motion.a>
              ))}
            </div>

            {/* Social Links */}
            <div>
              <h4 className="text-lg font-semibold text-slate-900 mb-4">Follow Us</h4>
              <div className="flex gap-4">
                {["Twitter", "LinkedIn", "GitHub"].map((social) => (
                  <motion.a
                    key={social}
                    href="#"
                    whileHover={{ scale: 1.1, y: -5 }}
                    className="w-12 h-12 bg-white rounded-full flex items-center justify-center shadow-md hover:shadow-lg transition-all duration-300 border border-slate-200 hover:border-green-600 text-slate-700 hover:text-green-600"
                  >
                    <span className="text-sm font-semibold">{social[0]}</span>
                  </motion.a>
                ))}
              </div>
            </div>
          </motion.div>

          {/* Contact Form */}
          <motion.div
            initial={{ opacity: 0, x: 50 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.7 }}
          >
            <form
              onSubmit={submit}
              className="bg-white p-8 md:p-10 rounded-3xl shadow-xl border border-slate-200 space-y-6"
            >
              {/* Name Field */}
              <div>
                <label className="block text-slate-700 font-semibold mb-2">
                  Your Name
                </label>
                <motion.div
                  animate={focused === "name" ? { scale: 1.02 } : { scale: 1 }}
                  transition={{ duration: 0.2 }}
                >
                  <input
                    type="text"
                    className="w-full px-4 py-3 rounded-lg border-2 border-slate-200 focus:border-green-600 focus:outline-none transition-all duration-300 bg-slate-50 focus:bg-white"
                    placeholder="John Doe"
                    value={s.name}
                    onChange={(e) => setS({ ...s, name: e.target.value })}
                    onFocus={() => setFocused("name")}
                    onBlur={() => setFocused("")}
                    required
                  />
                </motion.div>
              </div>

              {/* Email Field */}
              <div>
                <label className="block text-slate-700 font-semibold mb-2">
                  Your Email
                </label>
                <motion.div
                  animate={focused === "email" ? { scale: 1.02 } : { scale: 1 }}
                  transition={{ duration: 0.2 }}
                >
                  <input
                    type="email"
                    className="w-full px-4 py-3 rounded-lg border-2 border-slate-200 focus:border-green-600 focus:outline-none transition-all duration-300 bg-slate-50 focus:bg-white"
                    placeholder="john@example.com"
                    value={s.email}
                    onChange={(e) => setS({ ...s, email: e.target.value })}
                    onFocus={() => setFocused("email")}
                    onBlur={() => setFocused("")}
                    required
                  />
                </motion.div>
              </div>

              {/* Message Field */}
              <div>
                <label className="block text-slate-700 font-semibold mb-2">
                  Your Message
                </label>
                <motion.div
                  animate={focused === "message" ? { scale: 1.02 } : { scale: 1 }}
                  transition={{ duration: 0.2 }}
                >
                  <textarea
                    rows={5}
                    className="w-full px-4 py-3 rounded-lg border-2 border-slate-200 focus:border-green-600 focus:outline-none transition-all duration-300 bg-slate-50 focus:bg-white resize-none"
                    placeholder="Tell us about your project..."
                    value={s.message}
                    onChange={(e) => setS({ ...s, message: e.target.value })}
                    onFocus={() => setFocused("message")}
                    onBlur={() => setFocused("")}
                    required
                  />
                </motion.div>
              </div>

              {/* Submit Button */}
              <motion.button
                type="submit"
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                className="w-full px-8 py-4 bg-green-600 text-white font-semibold rounded-lg hover:bg-green-700 transition-all duration-300 shadow-lg hover:shadow-xl flex items-center justify-center gap-2"
              >
                Send Message
                <motion.svg
                  className="w-5 h-5"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  animate={{ x: [0, 5, 0] }}
                  transition={{ duration: 1.5, repeat: Infinity }}
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M14 5l7 7m0 0l-7 7m7-7H3"
                  />
                </motion.svg>
              </motion.button>

              {/* Status Message */}
              {status && (
                <motion.div
                  initial={{ opacity: 0, y: -10 }}
                  animate={{ opacity: 1, y: 0 }}
                  className={`text-center font-medium ${
                    status.includes("‚úÖ") ? "text-green-600" : "text-red-600"
                  }`}
                >
                  {status}
                </motion.div>
              )}
            </form>
          </motion.div>

        </div>

      </div>
    </section>
  )
}
